import { useTranslation } from "react-i18next";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import style from "./InternalFees.module.css";
import { useLocation, useNavigate, useParams } from "react-router-dom";

const InternalFeesCard = ({ fees }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const onDetail = () => {
    let url =
      getQuery.state?.urlOffSet === "Institute"
        ? `/q/${params.username}/institute/member/finance?type=internal-fee-detail`
        : `/q/${params.username}/member/staff/finance?type=internal-fee-detail`;
    navigate(url, {
      state: {
        ...getQuery.state,
        fsid: fees?._id,
        feesName: fees?.feeName ?? "",
      },
    });
  };
  return (
    <div className={style.ifc_container} onClick={onDetail}>
      <img src={`${assestsUserFinanceUrl}/fees-card.svg`} alt="fees icon" />
      <div className={style.ifc_container_inner}>
        <h6>
          {t("rs_only")} {fees?.feeAmount ?? 0}/-
        </h6>
        <p>{fees?.feeName ?? ""} </p>
      </div>
    </div>
  );
};

export default InternalFeesCard;
